@import url("https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,400;0,500;1,300;1,400&display=swap");

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  background-color: white;
  background: white;
  font-family: "Jost", sans-serif;

  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;*/
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App .honeLetter {
  margin-left: 100px;
  text-align: center;
  margin-bottom: 100px;
  font-family: "Jost", sans-serif;
  margin: 0;
  background-color: antiquewhite;
  padding: 100px;
  position: relative;
}

.honeLetter {
  border-end-end-radius: 20px;
  border-bottom-right-radius: 200px;
}

.Navbar a {
  color: rgb(17, 14, 14);
  text-decoration: none;
}
.HomeNav {
  padding-left: 100px;
  font-weight: lighter;
  margin-bottom: 10px;
}

.Welcompages {
  font-family: "Jost", sans-serif;
  padding-left: 70px;
  font-size: 50px;
  size: 20px;
  background-color: rgb(61, 237, 243);
}

.pagesHome {
  display: grid;
  grid-template-columns: 3 1fr;
  gap: 30px;
  font-family: "Jost", sans-serif;
  line-height: 5px;
  margin: 8px 24px;
  background-color: white;
  margin-top: 20px;
}

.datafetching-detail h4 {
  color: brown;
}

.datafetching-detail {
  margin-bottom: 1px 0px 0px 0px;
  border: 1px rgb(255, 247, 247) solid;
  background-color: rgb(253, 218, 171);
  padding: 0px 20px 20px;
  width: 30%;
  height: 100px;
  border-radius: 20px;
  cursor: pointer;
}

.datafetching-detail:hover {
  box-shadow: 2px 2px 2px 2px rgba(5, 5, 5, 0.3);
}

.Valid {
  font-family: "Jost", sans-serif;
}

.createInput {
  position: absolute;
  display: block;
  top: 50%;
  left: 15%;
  margin-left: 500px;

  cursor: pointer;
}

.inputsForm form input {
  display: block;
}

input {
  padding: 10px;
  width: 400px;
  border: none;
  border-bottom: 3px black solid;
  border-left: 3px black solid;
  border-radius: 30px;
}
.createInput label {
  line-height: 40px;
}

.OneInput {
  margin-bottom: 10px;
  
}


 .Valid{
  justify-items: center;
  margin: 3px 190px;
  cursor: pointer;
  border:  none;
  background: none;
  background-color: rgb(146, 119, 119);
  padding: 10px;
  width: 90px;
  border-radius: 5px;
}

.Valid:hover{
border-bottom-right-radius: 20px;
border-bottom: 4px black solid;
cursor: pointer;
}

div #error{
  padding: 10px;
  background: #ffefef;
  border: 1px solid blanchedalmond;
  color: aliceblue;
  border-radius: 6px;
  margin: 20px 0px;
  cursor: pointer;
}

input #error{
  border: 1px solid red;
}